#ViewLand>h4 {
    text-align: right;
    overflow: hidden;
    margin-inline-start: 25px;
    font-size: 25px;
    font-weight: 700;
}

#ViewLand>h4 .capital {
    font-size: 14px;
    font-weight: normal;
    color: #C0C0C0;
    position: absolute;
}

#ViewLand>h4 .VillageName {
    float: right;
    margin-inline-end: 10px;
}

#ViewLand table.info {
    text-align: right;
}

#ViewLand table.info .number {
    font-weight: bold;
    padding: 0px 7px;
}

#ViewLand table.info.oasis b {
    color: rgb(2, 122, 2);
}

#ViewLand .options {
    text-align: right;
    margin-top: 20px;
}

@media screen and (max-width: 960px) {
    #ViewLand>h4 {
        margin-top: 30px;
        margin-bottom: 20px;
    }
}